$wizard-done: lighten(theme-color('secondary'), 5%);
.wizard-main {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
}

.wizard-main .wizard-container {
    display: block;
    margin: 0;
    padding: 0;
    position: relative;
}

.wizard-main .step-content {
    display: none;
    position: relative;
    margin: 0;
}

.wizard-main .wizard-toolbar {
    margin-left: 0;
}

.wizard-theme-default {
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
}

.wizard-theme-default .wizard-container {
    min-height: 250px;
}

.wizard-theme-default .step-content {
    padding: 10px;
    border: 0px solid #D4D4D4;
    background-color: #FFF;
    text-align: left;
}

.wizard-theme-default .wizard-toolbar {
    background: #f9f9f9;
    border-radius: 0 !important;
    padding-left: 10px;
    padding-right: 10px;
    padding: 10px;
    margin-bottom: 0 !important;
}

.wizard-theme-default .wizard-toolbar-top {
    border-bottom-color: #ddd !important;
}

.wizard-theme-default .wizard-toolbar-bottom {
    border-top-color: #ddd !important;
}

.wizard-theme-default > ul.step-anchor > li{
    position: relative;
    margin-right: 2px;
}

.wizard-theme-default > ul.step-anchor > li > a, .wizard-theme-default > ul.step-anchor > li > a:hover {
    border: none !important;
    color: #bbb;
    text-decoration: none;
    outline-style: none;
    background: transparent !important;
    border: none !important;
}

.wizard-theme-default > ul.step-anchor > li.clickable > a:hover {
    color: #4285F4 !important;
    background: transparent !important;
}

.wizard-theme-default > ul.step-anchor > li > a::after {
    content: "";
    background: #4285F4;
 /* #5bc0de #4285F4*/
    height: 2px;
    position: absolute;
    width: 100%;
    left: 0px;
    bottom: 0px;
    -webkit-transition: all 250ms ease 0s;
    transition: all 250ms ease 0s;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
}

.wizard-theme-default > ul.step-anchor > li.active > a {
    border: none !important;
    color: #4285F4 !important;
    background: transparent !important;
}

.wizard-theme-default > ul.step-anchor > li.active > a::after {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}

.wizard-theme-default > ul.step-anchor > li.done > a {
    border: none !important;
    color: #000 !important;
    background: transparent !important;
}

.wizard-theme-default > ul.step-anchor > li.done > a::after {
    background: #5cb85c;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}

.wizard-theme-default > ul.step-anchor > li.danger > a {
    border: none !important;
    color: #d9534f !important;
    background: transparent !important;
}

.wizard-theme-default > ul.step-anchor > li.danger > a::after {
    background: #d9534f;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
}

.wizard-theme-default > ul.step-anchor > li.disabled > a, .wizard-theme-default > ul.step-anchor > li.disabled > a:hover {
    color: #eee !important;
}

@media screen and (max-width: 768px) {
    .wizard-theme-default > .nav-tabs > li {
        float: none !important;
    }
}

@-webkit-keyframes ld {
    0% {
        -webkit-transform: rotate(0deg) scale(1);
        transform: rotate(0deg) scale(1);
    }

    50% {
        -webkit-transform: rotate(180deg) scale(1.1);
        transform: rotate(180deg) scale(1.1);
    }

    100% {
        -webkit-transform: rotate(360deg) scale(1);
        transform: rotate(360deg) scale(1);
    }
}

@keyframes ld {
    0% {
        -webkit-transform: rotate(0deg) scale(1);
        transform: rotate(0deg) scale(1);
    }

    50% {
        -webkit-transform: rotate(180deg) scale(1.1);
        transform: rotate(180deg) scale(1.1);
    }

    100% {
        -webkit-transform: rotate(360deg) scale(1);
        transform: rotate(360deg) scale(1);
    }
}

.wizard-theme-default > ul.step-anchor > li.loading:before {
    content: '';
    display: inline-block;
    position: absolute;
    background: transparent;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: 2px solid #fff;
    border-top-color: transparent;
    border-bottom-color: transparent;
    border-left-color: #4285f4;
    border-right-color: #4285f4;
    top: 50%;
    left: 50%;
    margin-top: -16px;
    margin-left: -16px;
    width: 32px;
    height: 32px;
    -webkit-animation: ld 1s ease-in-out infinite;
    animation: ld 1s ease-in-out infinite;
}

.wizard-theme-dots .wizard-container {
    min-height: 300px;
}
.wizard-theme-dots .step-content {
    padding: 10px 0;
    border: none;
    background-color: #FFF;
    text-align: left;
}
.wizard-theme-dots .wizard-toolbar{
    background: #fff;
    border-radius: 0 !important;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 0 !important;
}
.wizard-theme-dots .wizard-toolbar-top{
    border-bottom-color: #ddd !important;
}
.wizard-theme-dots .wizard-toolbar-bottom{
    border-top-color: #ddd !important;
    border-bottom-color: #ddd !important;
}
.wizard-theme-dots > ul.step-anchor{
    position: relative;
    background: #fff;
    border: 0px solid #ccc !important;
    list-style: none;
}
.wizard-theme-dots > ul.step-anchor:before {
    content: " ";
    position: absolute;
    top: 70px;
    bottom: 0;
    width: 100%;
    height: 5px;
    background-color: #f5f5f5;
    border-radius: 3px;
    z-order: 0;
    z-index: 95;
}
.wizard-theme-dots > ul.step-anchor > li{
    border: none;
}
/* Anchors styles */
.wizard-theme-dots > ul.step-anchor > li > a{
    position: relative;
    text-align: center;
    font-weight: bold;
    background: transparent;
    border: none;
    color: #ccc;
    text-decoration: none;
    outline-style:none;
    z-index: 96;
    display: block;
}
.wizard-theme-dots > ul.step-anchor > li > a:before {
    content: ' ';
    position: absolute;
    bottom: 2px;
    left: 40%;
    margin-top: 10px;
    display: block;
    border-radius: 50%;
    color: theme-color('primary');
    background: #f5f5f5;
    border: none;
    width: 30px;
    height: 30px;
    text-decoration: none;
    z-index: 98;
}
.wizard-theme-dots > ul.step-anchor > li > a:after  {
    content: ' ';
    position: relative;
    left: 43%;
    bottom: 2px;
    margin-top: 10px;
    display: block;
    width: 15px;
    height: 15px;
    background: #f5f5f5;
    border-radius: 50%;
    z-index: 99;
}
.wizard-theme-dots > ul.step-anchor > li > a:hover  {
    color: #ccc;
    background: transparent;
}
.wizard-theme-dots > ul.step-anchor > li > a:focus{
    color: #ccc;
    border: none;
}
.wizard-theme-dots > ul.step-anchor > li.clickable > a:hover {
    color: #999;
}
/* Active anchors */
.wizard-theme-dots > ul.step-anchor > li.active > a{
    color: theme-color('primary');
}
.wizard-theme-dots > ul.step-anchor > li.active > a:hover{
    border: none;
}
.wizard-theme-dots > ul.step-anchor > li.active > a:after {
    background: theme-color('primary');
}
/* Done anchors */

.wizard-theme-dots > ul.step-anchor > li.done > a{
    color: $wizard-done;
}
.wizard-theme-dots > ul.step-anchor > li.done > a:after {
    background: $wizard-done;
}
/* Danger anchors */
.wizard-theme-dots > ul.step-anchor > li.danger > a {
    color: #d9534f;
}
.wizard-theme-dots > ul.step-anchor > li.danger > a:after {
    background: #d9534f;
}

.wizard-theme-dots > ul.step-anchor > li.disabled > a, .wizard-theme-dots > ul.step-anchor > li.disabled > a:hover {
    color: #eee !important;
}
.wizard-theme-dots > ul.step-anchor > li.disabled > a:after {
    background: #eee;
}

/* Responsive CSS */
@media screen and (max-width: 768px) {
    .wizard-theme-dots > ul.step-anchor:before {
        top: 0;
        bottom: 0;
        left: 10px;
        width: 5px;
        height: 100%;
        background-color: #f5f5f5;
        display: block;
        margin-right: 10px;
    }

    .wizard-theme-dots > ul.step-anchor > li{
        margin-left: 20px;
        display: block;
        clear: both;
    }

    .wizard-theme-dots > ul.step-anchor > li > a{
        text-align: left;
        margin-left: 0;
        display: block;
    }

    .wizard-theme-dots > ul.step-anchor > li > a:before {
        top: 5px;
        left: -23px;
        margin-right: 10px;
        display: block;
    }
    .wizard-theme-dots > ul.step-anchor > li > a:after  {
        top: -38px;
        left: -31px;
        margin-right: 10px;
        display: block;
    }
}

.wizard-theme-dots > ul.step-anchor > li.loading > a:before{
    -webkit-animation: help-bubble-pulse 1.5s linear infinite;
    -moz-animation:    help-bubble-pulse 1.5s linear infinite;
    -o-animation:      help-bubble-pulse 1.5s linear infinite;
    animation:         help-bubble-pulse 1.5s linear infinite;
}
.wizard-theme-dots > ul.step-anchor > li.loading > a:after{
    background-color: #ccc;
    opacity: 1;
    -webkit-animation: help-bubble-pulse 1.5s linear infinite;
    -moz-animation:    help-bubble-pulse 1.5s linear infinite;
    -o-animation:      help-bubble-pulse 1.5s linear infinite;
    animation:         help-bubble-pulse 1.5s linear infinite;
}

@keyframes help-bubble-pulse {
	0% {
		transform: scale(1);
		opacity: .75
	}

	25% {
		transform: scale(1);
		opacity: .75
	}

	100% {
		transform: scale(2.5);
		opacity: 0
	}
}
